<template>
    <div class="content">

    <div class="order-item">
        <p class="bg-primary title">
            <router-link to="/">订单编号：123123123</router-link>
            <span class="del" @click="del">删除</span>
        </p>
        <ul class="info">
            <li>下单时间：2018-9-9</li>
            <li>学校信息：白塔岭小学</li>
            <li>
                学生信息：
                小明
                男
                1年级 
                2班
            </li>
            <li>商品信息：100元</li>
            <li>商品状态：未支付</li>

            <li>
                <router-link to="/">确认收货地址</router-link>
            </li>

        </ul>
    </div>

</div>
</template>

<script>
export default {
    methods:{
        del(){
            if(window.confirm("是否删除？")){
                alert("删除成功")
            }
        }
    }
}
</script>


<style scoped>
.title{
    padding:3%
}
.del {
    float: right;
    color:#fff;
}

ul,li{
    margin:0px;
    padding:0px;
    list-style: none;
}
.order-item {
    margin-bottom:20px;
}

.detail{
    color:white;
}
a{
    color:#fff;
}
</style>




