<template>
  <div>
    <div class="content">
      <form class="form-inline">
        <div class="input-group">
          <input type="text" class="form-control" placeholder="请填写学校名称">
          <span class="input-group-btn">
            <button class="btn btn-primary" type="button">搜索</button>
          </span>
        </div>
      </form>
    </div>

    <div class="content">
      <h3 class="bg-primary title">小学</h3>
        <router-link :to='"/studentList/" + item.id' v-for="item in schools.primary">
            {{item.name}}
        </router-link>
      <h3 class="bg-primary title">初中</h3>
        <router-link :to='"/studentList/" + item.id' v-for="item in schools.middle">
            {{item.name}}
        </router-link>
      <h3 class="bg-primary title">高中</h3>
    </div>
  </div>
</template>

<script>
import { getSchoolList } from "@/api/schools"
import { getOrderList } from "@/api/orders"
import { getStudentList } from "@/api/students"
export default {
    data(){
        return {
            schools:{
                primary:[
                    {
                        id:"1",
                        name:"白塔岭小学"
                    }
                ],
                middle:[
                    {
                        id:"2",
                        name:"白塔岭小学"
                    }
                ],
                high:[],
            }
        }
    },
    created(){
        this.getList();
    },
    methods:{
        getList(){
            getSchoolList().then((res) =>{
                console.log(res.data)
            }).catch((err) => {
                console.log(err)
            })
            getOrderList().then((res) =>{
                console.log(res.data)
            }).catch((err) => {
                console.log(err)
            })
            getStudentList().then((res) =>{
                console.log(res.data)
            }).catch((err) => {
                console.log(err)
            })
        }
    }
}
</script>


<style scoped>
.title{
    padding:3%;
}
</style>




