import axios from "axios"

export function getStudentList(query) {
  // return axios.get(`/schools`,{
  //     params:query
  // })
  return axios.get(`${process.env.BASE_API}/wechat/students`)
}

export function createStudent(data) {
  return axios.post(`/schools`, {
    data: data
  })
}

export function deleteStudent(id) {
  return axios.delete(`/schools/${id}`)
}

export function updateStudent(id) {
  return axios.update(`/schools/${id}`,{
      data:data
  })
}




