<template>
  <div>
    <div class="content">
      <div class="btn-group" role="group" aria-label="...">
        <router-link to="/studentForm" class="btn btn-primary">添加学生</router-link>
        <router-link to="/" class="btn btn-primary">重新选择学校</router-link>
      </div>
    </div>
    <div class="content">
      <table class="table table-striped">
        <thead>
          <tr>
            <th>序号</th>
            <th>姓名</th>
            <th>年级</th>
            <th>班级</th>
            <th>修改</th>
            <th>确认</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item,index in students">
            <td>{{index + 1}}</td>
            <td>{{item.name}}</td>
            <td>{{item.grade}}</td>
            <td>{{item.clazz}}</td>
            <td>
              <router-link to="/">修改</router-link>
            </td>
            <td>
              <router-link to="/orderForm">确认</router-link>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      students: [
        {
          name: "小明",
          grade: 1,
          clazz: 2
        }
      ]
    };
  }
};
</script>




